@charset "UTF-8";

// ----------------------
// == Site settings
// ----------------------

// Fonts
@font-face {font-family: 'SurveyorFine-Bold-Pro'; font-weight: normal; font-style: normal;
    src: url('fonts/SurveyorFine-Bold-Pro.otf');
    src: url('fonts/SurveyorFine-Bold-Pro.eot?#iefix') format('embedded-opentype'),
         url('fonts/SurveyorFine-Bold-Pro.woff') format('woff'),
         url('fonts/SurveyorFine-Bold-Pro.ttf') format('truetype'),
         url('fonts/SurveyorFine-Bold-Pro.svg#SurveyorFine-Bold-Pro') format('svg');
}

$font-size-base:          18px !default;
$font-size-large:         ceil(($font-size-base * 1.25)) !default;
$font-size-small:         ceil(($font-size-base * 0.85)) !default;

$font-size-h1:            floor(($font-size-base * 3.4)) !default;
$font-size-h2:            floor(($font-size-base * 1.8)) !default;
$font-size-h3:            ceil(($font-size-base * 1.2)) !default;
$font-size-h4:            ceil(($font-size-base * 1.25)) !default;
$font-size-h5:            $font-size-base !default;
$font-size-h6:            ceil(($font-size-base * 0.85)) !default;

$font-size-h1-mobile:            floor(($font-size-base * 2.5)) !default;
$font-size-h2-mobile:            floor(($font-size-base * 1.5)) !default;
$font-size-h3-mobile:            ceil(($font-size-base * 1.15)) !default;

// = Colors
// -----------------------------

// Graphic chart
$facebook: #3b5998;
$twitter: #43bfe1;

$or: #be8d4b;
$parchment: #f0e5d1;
$blue: #253c95;
$muddy-waters: #bb9c64;
$laser: #c9a971;
$twine: #23507a;



// = Grid
// -----------------------------

// Max width wrapper
$wrapper-width: pxtoem(1140, 16);
