/* ==|====================
   Module/Boutons
   ======================= */

/* Animations */
.btn, .btn-a:hover, #btn-contact-fixed, #btn-contact-fixed:hover {transition: all 0.3s ease-in-out;}
a.btn, a.btn:visited, a.btn:focus, a.btn:hover {text-decoration: none; border-bottom: 0; color: white; box-shadow: 0;}

/* Boutons */
.btn {
  position: relative;
  margin: 0 auto;
  background: $twine;
  color: white;
  font-family: 'Roboto';
  font-size: 18px;
  letter-spacing: 0.02em;
  text-align: center;
  padding: 15px;
  text-decoration: none;
  border-radius: 0;
  transition: all .2s ease;
  &:hover {
    color: white;
    background: $laser;
    transition: all .2s ease;
  }
}
#btn-home {
  animation: mini_slide_top .3s cubic-bezier(.4,0,.2,1);
  animation-fill-mode:forwards;
  animation-duration:0.5s;
  animation-delay: 4s;
  opacity: 0;
}

#btn-contact-fixed {
  position: fixed;
  bottom: 40px; right: 40px;
  border-radius: 50%;
  z-index: 99;
  box-shadow: 0px 4px 7px -4px rgba(51,50,50,1);
  img {transition: all 0.2s ease-in-out;}
  img:hover {transform: scale(1.08); transition: all 0.2s ease-in-out;}
}

@media (max-width: 480px) {
  #btn-contact-fixed {display: none;}
}
