/* ==|====================
   Layout/Header
   ======================= */
header {
  .logo {
    position: fixed;
    top: 160px;
    left: -60px;
    height: 45px;
    transform: rotate(-90deg);
    z-index: 10;
  }
  nav {
    position: fixed;
    width: 100%; height: 140px;
    top: 0;
    background: white;
    transition: all 0.3s ease-in-out;
    z-index: 9;
    & .menu {
    position: absolute;
    left: 175px;
    float: left;
      & li {
        position: relative;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 500;
        display: inline-block;
        margin: 0 28px 0 28px;
        z-index: 5;
        & a {
          padding: 60px 0px 65px 0px;
          display: block;
          color: black;
          &:hover, &.focus, &.visited {text-decoration: none;}
        }
        &:first-child a {margin-left: 0; padding-left: 0;}
      }
    }
  }
  ul.social-medias {
    position: fixed;
    bottom: 30px;
    z-index: 4;
    & li {
      list-style: none;
      padding: 17px 0 17px 0;
      margin: 0 20px 0 20px;
      &:first-child {margin: 0 16px 0 24px;}
    }
  }
  .ripple-1, .ripple-2, .ripple-3, .ripple-4, .ripple-5 {
  	position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
  	width: 100vw;
  	height: 100vh;
  	overflow: hidden;
    background-color: white;
    clip-path: circle(30px at 270px 40px);
    transition: .3s;
  	z-index: -1;
  }
  .ripple-2 {clip-path: circle(30px at 400px 40px);}
  .ripple-3 {clip-path: circle(30px at 550px 40px);}
  .ripple-4 {clip-path: circle(30px at 710px 40px);}
  .ripple-5 {clip-path: circle(30px at 95vw 94vh);}
  .active {clip-path: circle(100% at center); transition: .3s; z-index: 1;}
}

.nav-top {top: 0; transition: all 0.3s ease-in-out;}
.nav-up {top: -140px; transition: all 0.3s ease-in-out;}

.menu-mobile {
  position: fixed;
  display: none;
  top: 0;
  width: 50%; height: 100vh;
  background: white;
  z-index: 8;
  ul {
    padding-top: 180px;
    padding-right: 32px;
    & li {
      padding-bottom: 28px;
      font-family: 'Roboto';
      font-size: 22px;
      line-height: 1;
      font-weight: 600;
      list-style: none;
      letter-spacing: 0.01em;
      text-align: right;
      cursor: pointer;
      & a {color: black;}
      & a:hover {
        background-position: 0 0;
        background-size: 0 0;
        text-decoration: none;
      }
    }
  }
}
#pushmenu-left {
  right: -50%;
  transition: all 0.3s ease;
  &.open {right: 0; transform: translate3d(0, 0, 0); transition: all 0.3s ease;}
}
#overlay-mobile-menu {
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
  background: black;
  width: 100vw; height: 100vh;
  visibility: hidden;
  opacity: 0;
  z-index: 7;
  content: '';
  transition: all 0.3s ease;
  &.active {visibility: visible; opacity: 0.4; transition: all 0.3s ease;}
}

ul.menu {
  li {
    transition-duration: 0.2s;
    position: relative;
    &:before, &:after {
      content: '';
      position: absolute;
      bottom: 60px;
      width: 0;
      height: 2.2px;
      margin: 3px 0 0;
      transition: all 0.2s ease-in-out;
      transition-duration: 0.2s;
      opacity: 0;
    }
    &:before {left: calc(50%); background-color: desaturate(black, 40%);}
    &:after {right: calc(50%); background-color: desaturate(black, 40%);}
    &:hover {
      cursor: pointer;
      &:before, &:after {width: 50%; opacity: 1;}
    }
  }
}

header {
  .menu-mobile, ul.social-medias {
    li {
      &:before, &:after {display: none;}
    }
    li a {
      transition-duration: 0.2s;
      position: relative;
      &:before,
      &:after {
        content: '';
        position: absolute;
        bottom: 60px;
        width: 0;
        height: 1.8px;
        margin: 3px 0 0;
        bottom: -5px;
        transition: all 0.2s ease-in-out;
        transition-duration: 0.2s;
        opacity: 0;
      }
      &:before {left: calc(50%); background-color: desaturate(black, 40%);}
      &:after {right: calc(50%); background-color: desaturate(black, 40%);}
      &:hover {
        cursor: pointer;
        &:before, &:after {width: 50%; opacity: 1;}
      }
    }
  }
}

header {
  ul.social-medias {
    li a {
      &:before, &:after {display: none;}
    }
  }
}

.hamburglar {
  position: absolute;
  top: 12px; right: 30px;
  transform: scale(1);
  display: none;
  width: 68px;
  height: 68px;
  user-select: none;
  z-index: 9999999;
  cursor: pointer;
}
#top, #bottom {
  position: absolute;
  display: block;
  height: 3px;
  width: 36px;
  border-radius: 2px;
  background: black;
}
#top {top: 23px; left: 16px;}
#bottom {top: 41px; left: 16px;}
path, line {
  stroke: black;
  stroke-linecap: round;
}
#top, #bottom {
  transform-origin: 34px 2px;
  transform: rotate(0deg);
  transition: all 500ms cubic-bezier(0.8, -1.1, 0.5, 1.9);
}
#circle {
  stroke-dasharray: 1 100 32 300;
  stroke-dashoffset: 101;
  transition: all 750ms ease;
}
.hamburglar.is-closed #circle {
  stroke-dasharray: 1 100 190 300;
  stroke-dashoffset: 1;
}
.hamburglar.is-closed #top {transform: translateX(-4.5px) rotate(-45deg); top: 22px;}
.hamburglar.is-closed #bottom {transform: translateX(-4.5px) rotate(45deg); top: 44px;}

@media (max-width: 1600px) {
  header {
    .logo {top: 120px;}
    nav {
      height: 90px;
      & .menu {
        left: 95px;
        & li a {padding: 30px 0px 30px 0px;}
      }
    }
    .ripple-1 {clip-path: circle(30px at 190px 40px);}
    .ripple-2 {clip-path: circle(30px at 320px 40px);}
    .ripple-3 {clip-path: circle(30px at 470px 40px);}
    .ripple-4 {clip-path: circle(30px at 630px 40px);}
  }
  ul.menu li {
    &:before, &:after {bottom: 27px;}
  }
}

@media (max-width: 1200px) {
  .menu-mobile {display: block;}
  header {
    .logo {
      position: absolute;
      top: 20px; left: 0; right: 0;
      margin: 0 auto;
      transform: rotate(0deg);
      z-index: 9999999;
    }
    .ripple-1, .ripple-2, .ripple-3, .ripple-4 {display: none;}
    nav {display: none;}
    ul.social-medias {
      top: 400px;
      right: -50%;
      bottom: auto;
      z-index: 9;
      transition: all 0.3s ease;
      &.active {right: 22px; transform: translate3d(0, 0, 0); transition: all 0.3s ease;}
      & li {
        display: inline-block;
        padding: 8px 0 8px 0;
        margin: 0 8px 0 8px;
      }
    }
  }
  .hamburglar {display: block;}
}

@media (max-width: 768px) {
  .hamburglar {top: 10px; right: 20px;}
}
@media (max-width: 480px) {
  .hamburglar {right: 20px;}
  header {
   .logo {top: 23px; width: 160px; height: auto;}
   ul.social-medias {top: 300px;}
   ul.social-medias.active {right: 10px;}
  }
  .menu-mobile {
    ul {
      padding-left: 0;
      padding-right: 15px;
      padding-top: 110px;
      & li {padding-bottom: 28px; font-size: 18px;}
    }
  }
}
