/* ===========================
   Base
   =========================== */

::selection {background: $laser; color: white;}
* {box-sizing: border-box;}
body.fixed {position: fixed;}

.row {margin: 0; padding: 0;}

// Meilleur rendu des fonts
h1, h2, h3, h4, h5, h6, li, p {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto';
}
.l-content, .l-content-blog, p.description-contact, p.description-blog {
  a {
    color: $twine;
    display: inline;
    font-size: 18px;
    text-shadow: 1px 1px 0 #f5f6f9, -1px 1px 0 #f5f6f9, -1px -1px 0 #f5f6f9, 1px -1px 0 #f5f6f9;
    background-image: linear-gradient(90deg, currentColor 100%, transparent 100%);
    background-position: 0 1.04em;
    background-repeat: repeat-x;
    background-size: 1px 2px;
    padding-bottom: 2px;
    transition: all .2s ease;
    &::selection {
    background-color: rgba(0, 150, 255, 0.3);
    text-shadow: none;
    }
    &:hover {
      background-image: linear-gradient(90deg, $laser 100%, transparent 100%);
      text-decoration: none;
      background-position: 0 1.02em;
      background-size: 1px 1px;
      padding-bottom: 3px;
      transition: all .2s ease;
    }
  }
}
.pretty-text-underline::selection {
 background-color: rgba(0, 150, 255, 0.3);
 text-shadow: none;
}
.l-box-contact {
  max-width: 600px;
  width: 100%;
  margin: 120px 0 0 180px;
  & .col-md-6 {padding: 0 65px 0 0; margin: 0;}
  & h3 {
    padding-bottom: 40px;
    font-family: 'Roboto';
    font-size: 52px;
    line-height: 0.9;
    letter-spacing: -0.015em;
    color: black;
  }
}

a, a:visited, a:focus, a:hover {text-decoration: none; border-bottom: 0;}
a.svg {
 position: relative;
 z-index: 4;
 &:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
  }
}

strong, b {font-weight: 700;}
p, ul li, ol li {font-size: 17px; line-height: 1.4; color: #333232;}
p.copyright {
  font-weight: 500;
  padding: 180px 0 40px 0;
  text-align: center;
  a {color: black;}
  &:hover {text-decoration: none;}
}
h2, h3, h4 {font-weight: 600;}
h2 {margin-top: 30px; font-size: $font-size-h2;}
h3 {font-size: $font-size-h3;}

aside {
  position: fixed;
  float: left;
  width: 240px; height: 100vh;
  z-index: 3;
}
.l-content, #l-content-html-css, #l-content-wordpress, #l-content-react {
  max-width: 960px;
  width: 100%;
}

main.accueil {
  position: fixed;
  padding: 0 0 0 240px;
  width: 100%; height: 100vh;
  float: right;
  .col-md-6 {padding: 0; margin: 0;}
  #col-gauche {padding-right: 80px;}
  .vertical-align {
    position: relative;
    display: table-cell;
    vertical-align: middle;
    min-height: 100vh; height: 100vh;
    z-index: 2;
    & img {position: relative; margin: 0 auto; top: 13rem; display: table;}
  }
  #background-home {
    float: right;
    width: 50%;
    height: 100vh;
    background-size: cover;
    background-image: url('http://julienlucas.com/wp-content/uploads/2017/10/julien-lucas-developpeur-front-end-wordpress-freelance-6.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    animation:fade_opacity ease-in 1;
    animation-fill-mode:forwards;
    animation-duration:0.5s;
    animation-delay: 4s;
    opacity: 0;
    &:after {
      content: '';
      display: block;
      width: 100%;
      padding-top: 33.333%;
    }
  }
  h1.headline-home {
    margin-bottom: 20px;
    font-size: $font-size-h1;
    font-weight: 700;
    padding-right: 5%;
    color: black;
    & span.or {color: $twine;}
  }
  p {font-size: 20px; padding-right: 5%; margin-bottom: 15px;}
  #description-home {
    animation: mini_slide_top .3s cubic-bezier(.4,0,.2,1);
    animation-fill-mode:forwards;
    animation-duration:0.5s;
    animation-delay: 4s;
    opacity: 0;
  }
  .visually-hidden {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px; width: 1px;
    margin: -1px; padding: 0; border: 0;
  }
  .typed-cursor{display: none;}
  .work-accueil {
    width: 70%;
    animation: mini_slide_top .3s cubic-bezier(.4,0,.2,1);
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
    animation-delay: 4s;
    opacity: 0;
  }
}

main.services, main.blog, main.apropos, main.contact, main.travail, main.page, main.single {
  position: relative;
  padding: 0 240px 0 240px;
  float: right;
  width: 100%; height: 100vh;
  z-index: -1;
  .l-content, #l-content-html-css, #l-content-wordpress, #l-content-react {margin-top: 45px; min-height: 230px;}
  #l-content-html-css, #l-content-wordpress, #l-content-react {
    strong {color: $twine;}
  }
  #l-content-html-css {
    margin-bottom: 80px;
    background-repeat: no-repeat;
    background-position: right 0;
    background-image: url('images/logo-html-css.png');
    background-size: 230px;
  }
  #l-content-wordpress {
    margin-top: 10px;
    padding-bottom: 80px;
    background-repeat: no-repeat;
    background-position: right 0;
    background-image: url('images/logo-wordpress.png');
    background-size: 180px;
  }
  #l-content-react {
    background-repeat: no-repeat;
    background-position: right 0;
    background-image: url('images/logo-react.png');
    background-size: 180px;
  }
  #l-content-optimisations {
    ul {margin-top: 50px;}
    li {font-size: $font-size-base; margin-bottom: -5px;}
  }
  #optimisation-row {padding-top: 10px; padding-bottom: 0; margin-top: -40px;}
  #l-projets {
    position: relative;
    margin-top: 60px;
    .col-md-4 {
      &.projet-1, &.projet-4 {padding: 0 20.75px 0 0;}
      &.projet-2, &.projet-5 {padding: 0 7.25px 0 7.25px;}
      &.projet-3, &.projet-6 {padding: 0 0 0 20.75px;}
      img {position: relative; margin: 0 auto; display: table; z-index: -1; cursor: pointer; transition: all .3s ease;}
      &:hover img {filter:brightness(75%); transition: all .3s ease;}
    }
    h3 {
      margin-top: 30px;
      font-size: 30px;
      font-weight: 700;
      text-align: center;
      color: black;
    }
    h4 {
      font-weight: 400;
      text-align: center;
      color: #7b7b7b;
    }
    p {
      position: relative;
      margin: 0 auto;
      display: table;
      width: 80%;
      font-size: 22px;
      line-height: 1.2;
      text-align: center;
      color: $or;
    }
  }
  p.description-blog, p.description-contact {
    position: relative;
    top: 210px;
    font-size: 20px;
    line-height: 1.3;
    color: black;
    & a {font-size: 20px;}
  }
  p.description-contact {
    top: 190px;
    font-size: $font-size-h4;
    line-height: 1;
    font-weight: 600;
    text-align: right;
    color: #333232;
    & span {font-size: $font-size-h6; font-weight: 400;}
  }
  h1.headline {
    position: relative;
    top: 150px;
    margin-bottom: 200px;
    max-width: 960px;
    width: 100%;
    font-family: 'Roboto';
    font-weight: 700;
    font-size: $font-size-h1;
    line-height: 1.02;
    letter-spacing: -0.03em;
    color: black;
  }
  & h2 {color: $twine; padding-top: 45px;}
  & h2.sub-headline {padding-top: 0;}
  & h2.blockquote {margin-top: 5px; padding-top: 0; color: $twine; line-height: 1.3;}
  & h2.services {
    padding-top: 40px;
    margin-bottom: 0;
    font-family: 'Roboto';
    font-size: 52px;
    line-height: 0.9;
    color: black;
    letter-spacing: -0.015em;
  }
}

main.apropos {
  h1.headline {margin-bottom: 150px;}
  h2 {margin-bottom: 4rem; color: black;}
  strong {color: $twine;}
  img {
    background: #ebebeb;
    float: left;
    width: 100%; max-width: 340px; height: auto;
    margin: 5px 3.5rem 3.5rem 0;
  }
  .l-box-contact {margin-top: 200px;}
  & a {font-size: 17px;}
}
main.travail {
  p {padding-bottom: 40px;}
  h2 {color: black;}
  .l-content {float: left; width: 70%; margin-bottom: 200px;}
  .aside {
    padding: 40px 0 0 50px;
    float: right;
    width: 30%;
    & p {font-size: 16px;}
    & h3 {margin-top: -10px; margin-bottom: 0px;}
  }
  img {
    margin-bottom: 60px;
    width: 100%;
    max-width: 960px;
    box-shadow: 0px 0px 52px 5px rgba(204,204,204,1);
  }
}
main.blog {
  .l-content-blog {
    h2 a {font-size: $font-size-h2;}
  }
}
main.contact {
  .l-content {margin-top: 0;}
  .col-md-7 {padding: 0; margin: 0;}
  .col-md-6 {padding: 0; margin: 0; padding-right: 10px;}
  .title-file {font-weight: 600; font-size: 20px; margin-top: -13px; bottom: -12px; position: relative; font-family: 'Roboto';}
  em {font-size: 16px; bottom: 5px; position: relative; font-family: 'Roboto';}
  .file {font-size: 14px; font-family: 'Roboto';}
}
main.services {
  .col-md-4 {margin: 0; padding: 0;}
  & h2 {padding-top: 0;}
  .logos-clients-slideshow {
    height: 140px;
    width: 100%;
    margin: 0 auto;
    margin-top: 0;
    position: relative;
    overflow: hidden;
    transform: translate3d(0, 0, 0);
    &:before {
      position: absolute;
      content: " ";
      top: 0;
      left: 0;
      width: 100%;
      height: 140px;
      z-index: 3;
      background-image: -webkit-repeating-linear-gradient(left, white 2%, rgba(183, 183, 183, 0) 20.6%, rgba(183, 183, 183, 0) 78.6%, white 98%, white 100%, white 100%);
      background-image: repeating-linear-gradient(left, white 2%, rgba(183, 183, 183, 0) 20.6%, rgba(183, 183, 183, 0) 78.6%, white 98%, white 100%, white 100%);
      background-image: linear-gradient(left, white 2%, rgba(183, 183, 183, 0) 20.6%, rgba(183, 183, 183, 0) 78.6%, white 98%, white 100%, white 100%);
      pointer-events: none;
    }
    > div {
      height: auto;
      width: 12526px;
      background: url('/wp-content/uploads/2018/01/developpeur-front-end-wordpress-clients-referents-1.jpg');
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      transform: translate3d(0, 0, 0);
    }
    & .mover {animation: moveSlideshow 140s linear infinite;}
  }
  @keyframes moveSlideshow {
    100% {transform: translateX(-66.6666%);}
  }
}

main.page, main.single {
  h2 {margin-bottom: 20px; padding-bottom: 0;}
  .author-box {
    img {position: relative; margin: 30px auto; margin-top: 70px; display: table;}
    p {position: relative; margin: 0 auto; display: table; max-width: 520px; width: 100%; text-align: center;}
  }
}
main.blog, main.single {
  p.date {font-style: italic; padding-top: 0; font-weight: 500; letter-spacing: 0; font-size: 16px; font-family: 'Roboto';}
}
 main.single {
  p.date {padding-top: 20px;}
  p, ul li, ol li, a {font-size: $font-size-base;}
}

/* Contact form 7 */
.wpcf7 input[type="text"]::-webkit-input-placeholder,
.wpcf7 input[type="email"]::-webkit-input-placeholder,
.wpcf7 textarea::-webkit-input-placeholder, .wpcf7 select::-webkit-input-placeholder,
.wpcf7 input[type="tel"]::-webkit-input-placeholder {
  color: #6b6b6b;
}
.wpcf7 input[type="text"], .wpcf7 input[type="email"], .wpcf7 input[type="tel"], .wpcf7 select {
  position: relative;
  width: 100%; height: 50px;
  padding: 4px 0 2px 15px;
  box-shadow: 0;
  border: 0;
  background: #ebebeb;
  appearance:none;
  transition: all .2s ease;
}
.wpcf7 textarea {
  max-width: 950px;
  width: 100%;
  padding: 15px 0 0 15px;
  box-shadow: 0;
  border: 0;
  appearance:none;
  background: #ebebeb;
  transition: all .2s ease;
}
.textarea {margin-right: 10px;}
.wpcf7 select {
  width: 100%;
  font: 16px/18px;
  appearance:none;
  box-shadow: 0;
  border: 0;
  color: #7b7b7b;
  background: #ebebeb url('/img/arrow-drop-down.png') 96% 20px no-repeat;
  background-image: 15px;
  cursor: pointer;
  transition: all .2s ease;
}
.wpcf7 input[type="text"]:focus, .wpcf7 input[type="email"]:focus,
.wpcf7 input[type="tel"]:focus, .wpcf7 textarea:focus, .wpcf7 select:focus {
  background: #dedede;
  transition: all .2s ease;
}
.wpcf7 input[type="submit"] {
  float: right;
  width: auto;
  display: block;
  text-align: center;
  font-family: 'Roboto';
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.02em;
  color: white;
  padding: 35px;
  border-radius: 0;
  border: 0;
  text-decoration: none;
  background: $twine;
    transition: all .2s ease;
  &:hover {
    color: white;
    background: $laser;
    transition: all .2s ease;
  }
}
.wpcf7-not-valid-tip {
  position: relative;
  margin-top: 14px;
  margin-bottom: -19px;
  font: 11px/13px;
}
.wpcf7-validation-errors {
  font-size: 11px;
  color: red;
  border: 0;
  display: none;
}
.wpcf7-not-valid, .wpcf7-select .wpcf7-not-valid, .wpcf7 select.wpcf7-not-valid,
.wpcf7 input[type=text].wpcf7-not-valid, .wpcf7 input[type=email].wpcf7-not-valid,
.wpcf7 input[type=tel].wpcf7-not-valid, .wpcf7 textarea.wpcf7-not-valid {
  border: 2px solid red;
}
.wpcf7-response-output.wpcf7-mail-sent-ok, .wpcf7-response-output.wpcf7-validation-errors,
.wpcf7-response-output.wpcf7-spam-blocked {
  background: transparent;
  text-align: center;
  border: 0;
  font-size: 17px;
}

/* Animations */
.o-heading-line {
  display:block;
  overflow:hidden;
  padding-bottom:.12em;
  margin-bottom:-.12em;
}
.o-heading_text {
  display:inline-block;
}
.delai-1, .delai-2, .delai-3, .delai-4, .delai-5, .delai-6, .delai-7, .delai-8, .delai-9 {
  animation: slide_top .5s cubic-bezier(.4,0,.2,1);
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
  opacity: 0;
}
.delai-1 {animation-delay: .1s;}
.delai-2 {animation-delay: .2s;}
.delai-3 {animation-delay: .3s;}
.delai-4 {animation-delay: .4s;}
.delai-5 {animation-delay: .5s;}
.delai-6 {animation-delay: .6s;}
.delai-7 {animation-delay: .7s;}
.delai-8 {animation-delay: .8s;}
.delai-9 {animation-delay: .9s;}

@keyframes fade_opacity {
  from { opacity:0; } to { opacity:1; }
}
@keyframes mini_slide_top {
  from {opacity:0; transform:translate3d(0,5%,0);}
  to {opacity:1; transform:none;}
}
@keyframes slide_top {
  from {opacity:0; transform:translate3d(0,75%,0);}
  to {opacity:1; transform:none;}
}
@keyframes slide_bottom {
  from {opacity:0; transform:translate3d(0,-75%,0);}
  to {opacity:1; transform:none;}
}
.link{
  position: fixed;
  bottom: 0;
  left: 0;
  margin: 10px;
  font-family: sans-serif;
  font-weight: 100;
  a {
    color: black;
    text-decoration: none;
    &:hover{text-decoration: underline;}
  }
}
svg {position: relative; margin: 0 auto; width: 100%; height: 100%;}

.name {transform-origin: 50% 50%; transform: scale(0.75);}

@media (max-width: 1600px) {
  p, ul li, ol li {font-size: 16px;}
  main.accueil, main.services, main.blog, main.apropos, main.page, main.single, main.contact, main.travail {
    padding: 0 160px 0 160px;
     ul li, ol li {padding-bottom: 0;}
     h1.headline {top: 120px; margin-bottom: 150px;}
  }
  main.accueil {
    padding: 0 0 0 160px;
    h1.headline-home {font-size: 44px;}
    p {font-size: 18px;}
    #col-gauche {padding-right: 20px;}
  }
   main.services {
     ul.detail {position: relative; margin-top: -30px;}
     #l-content-optimisations {
       margin: 0; padding: 0;
       &.col-md-6 {margin: 0; padding: 0;}
       &.row {margin: 0; padding: 0;}
     }
   }
}

@media (max-width: 1200px) {
  main.accueil, main.services, main.blog, main.apropos, main.contact, main.travail {padding: 0 55px 0 55px;}
  main.accueil {padding: 0 0 0 55px;}
  aside {display: none;}
}
@media (max-width: 990px) {
  .l-box-contact {
    margin: 120px 0 0 0;
    & .col-md-6 {padding: 0 65px 0 0;}
    h3 {padding-bottom: 10px;}
  }
  main.contact {
    .col-md-5 {margin-top: -200px;}
    .wpcf7 {padding-top: 170px;}
  }
  main.accueil, main.services, main.blog, main.apropos, main.page, main.single, main.contact, main.travail {
    h1.headline-home, h1.headline {font-size: $font-size-h1-mobile; top: 70px; margin-bottom: 100px;}
    h2, h2.sub-headline {font-size: $font-size-h2-mobile;}
    h2.services {font-size: 38px;}
    h3, h2.blockquote {font-size: $font-size-h3-mobile;}
    .l-box-contact {
      h3 {font-size: 38px;}
    }
    #l-projets {
      margin-top: 0;
      & #projet-1, & #projet-2, & #projet-3, & #projet-4, & #projet-5, & #projet-6 {padding: 30px 0 0 0;}
      h3 {font-size: 24px;}
      p {font-size: 18px;}
     }
   }
   main.blog {
    p.description-blog {top:0px;}
   }
 }
@media (max-width: 768px) {
 main.accueil {
    position: absolute;
    padding: 0;
    height: 100%;
    float: none;
   #background-home {float: none; width: 100%; height: auto; background-image: none;}
   .col-md-6 {display: none;}
   .col-md-6#col-gauche {padding: 60px 40px 0 40px; display: block;}
   h1.headline-home {margin-bottom: 5px;}
   .vertical-align {height: auto;}
 }
}

@media (max-width: 680px) {
  main.accueil, main.services, main.blog, main.apropos, main.contact, main.travail {padding: 0 25px 0 25px;}
  main.travail {
    .l-content {float: none; width: 100%;}
    .aside {float: none; width: 100%; padding: 40px 0 0 0;}
  }
}
@media (max-width: 480px) {
  .l-box-contact {
    margin: 30px 0 0 0;
  }
  main.accueil {
    .col-md-6#col-gauche {padding: 60px 20px 0 20px;}
  }
}
